import { ChangeDetectorRef, Component, OnInit } from '@angular/core';
import { DocumentReference, DocumentData } from '@angular/fire/firestore';
import { Course, Order, Test } from 'src/app/models/course.interface';
import { CommonService } from 'src/app/services/common/common.service';
import { DataService } from 'src/app/services/data/data.service';
import { StateService } from 'src/app/services/state/state.service';

@Component({
  selector: 'app-purchased-tests',
  templateUrl: './purchased-tests.page.html',
  styleUrls: ['./purchased-tests.page.scss'],
})
export class PurchasedTestsPage implements OnInit {

  public tests: Test[] = [];
  public course: Course = null;
  public schedule: any = null;
  public segment: string = 'schedule';
  public zoom: number = 1;

  constructor(
    private dataService: DataService,
    private cd: ChangeDetectorRef,
    private commonService: CommonService,
    private stateService: StateService
  ) {
    this.course = this.stateService.getData('course');
    this.dataService.getTests(this.course.id).subscribe(res => {
      this.tests = res;
      this.cd.detectChanges();
    });
    this.dataService.getScheduleByCourseId(this.course?.id).subscribe((resp: any) => {
      this.schedule = resp[0];
      this.zoom = this.segment === 'schedule' ? 1 : this.zoom;
      this.cd.detectChanges();
    });
  }

  ngOnInit() { }

  pageRendered(event: any) {
    // console.log('event::::::::::::::::::::\n', event);
  }

  zoomIn() {
    this.zoom = this.zoom === 0 ? 1 : this.zoom + 1;
  }

  zoomOut() {
    this.zoom = this.zoom - 1;
    if (this.zoom === 0) {
      this.zoom = 1;
    }
  }

  openTest(test: Test) {
    this.stateService.setData('test', test);
    this.commonService.navigateForward('purchased-test');
  }
}
